import React from "react"
import PropTypes from "prop-types"

import IconLinkedIn from "./linkedin"
import IconGitHub from "./github"
import IconExternal from "./external"
import IconMail from "./mail"
import IconInfo from "./info"

// Utility function to grab Icons by name
const Icon = ({ name, color }) => {
  switch (name.toLowerCase()) {
    case "linkedin":
      return <IconLinkedIn color={color} />
    case "github":
      return <IconGitHub color={color} />
    case "external":
      return <IconExternal color={color} />
    case "mail":
      return <IconMail color={color} />
    case "info":
      return <IconInfo color={color} />
    default:
      return null
  }
}

Icon.propTypes = {
  name: PropTypes.string.isRequired,
  color: PropTypes.string,
}

export default Icon
